	methods:{
		transferTime(time){
			var date = new Date(parseInt(time) * 1000);
			let y = date.getFullYear();
			let m = date.getMonth()+1;
			m = m < 10 ? ('0' + m) : m;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let newDate = y+ "-" +m+ "-" +d;
			return newDate
		},
		handleRules(){
			goWindow("/other_pages/help/index?help_id=",14)
		}
	}
}
